.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3%;
    font-family: sans-serif;
}

.buttonContainer {
    margin: 3% 2% 2% 2%;
    text-align: right;
    display: flex;
    flex-direction: column;
}

.button {
    padding: 10px 20px;
    background-color: #3d8d36;
    color: black;
    font-size: 125%;
    border: 2px solid black;
    cursor: pointer;
    border-radius: 10px;
}

.button:hover {
    background-color: lightgreen;
}

.card {
    background-color: lightgrey;
    padding: 10px 20px 10px 20px;
    width: 30vw;
    height: 15vw;
    color: black;
    font-size: 125%;
    border: 2px solid black;
    cursor: pointer;
    border-radius: 10px;
    margin: 2%;
    word-wrap: break-word;
    overflow: auto;
}

.inputField {
    background-color: lightgrey;
    width: 30vw;
    height: 7vw;
    color: black;
    font-size: 125%;
    border: 2px solid black;
    cursor: pointer;
    border-radius: 10px;
    margin: 2%;
    word-wrap: break-word;
    overflow: auto;
}

.correctInput {
    background-color: green;
}

.incorrectInput {
    background-color: red;
}

.fehlerMeldung {
    color: red;
}

.deckName {
    text-overflow: ellipsis;
    overflow: hidden;
    text-wrap: nowrap;
    max-width: 50vw;
}

.backArrow {
    margin-left: 2%;
    font-size: 200%;
    color: black;
    position: absolute;
}

.backArrow:hover {
    color: darkgrey;
}